'use client'

import { useState, useEffect } from 'react'
import DashboardLayout from '@/components/layout/DashboardLayout'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Button } from '@/components/ui/button'
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from '@/components/ui/table'
import { Badge } from '@/components/ui/badge'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select'
import { Textarea } from '@/components/ui/textarea'
import { Dialog, DialogContent, DialogDescription, DialogHeader, DialogTitle, DialogTrigger } from '@/components/ui/dialog'
import { Alert, AlertDescription } from '@/components/ui/alert'
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs'
import { mockStudents, mockThesisScores, mockThesisDrafts, mockUsers, User } from '@/lib/mockData'

export default function AdminProdiDashboard() {
  const [user, setUser] = useState<User | null>(null)
  const [loading, setLoading] = useState(true)
  const [addStudentDialog, setAddStudentDialog] = useState(false)
  const [uploadDialog, setUploadDialog] = useState(false)
  const [message, setMessage] = useState('')
  const [submitting, setSubmitting] = useState(false)

  // Add student form state
  const [studentForm, setStudentForm] = useState({
    nim: '',
    name: '',
    thesis_title: '',
    dosen_pembimbing_id: '',
    dosen_penguji_id: ''
  })

  // Upload form state
  const [uploadForm, setUploadForm] = useState({
    student_id: '',
    file: null as File | null
  })

  useEffect(() => {
    // Get user from session - in production, verify with API
    const mockUser: User = {
      id: 4,
      username: 'adminprodi1',
      email: 'adminprodi1@university.edu',
      role: 'admin_prodi',
      full_name: 'Admin Prodi Informatika'
    }
    setUser(mockUser)
    setLoading(false)
  }, [])

  const handleAddStudent = async (e: React.FormEvent) => {
    e.preventDefault()
    setSubmitting(true)
    setMessage('')

    try {
      // Validate form
      if (!studentForm.nim || !studentForm.name || !studentForm.thesis_title || 
          !studentForm.dosen_pembimbing_id || !studentForm.dosen_penguji_id) {
        setMessage('Semua field harus diisi')
        return
      }

      // In production, send to API
      console.log('Adding student:', studentForm)
      
      setMessage('Mahasiswa berhasil ditambahkan')
      setStudentForm({
        nim: '',
        name: '',
        thesis_title: '',
        dosen_pembimbing_id: '',
        dosen_penguji_id: ''
      })
      
      setTimeout(() => {
        setAddStudentDialog(false)
        setMessage('')
      }, 2000)
    } catch (error) {
      setMessage('Terjadi kesalahan sistem')
    } finally {
      setSubmitting(false)
    }
  }

  const handleFileUpload = async (e: React.FormEvent) => {
    e.preventDefault()
    setSubmitting(true)
    setMessage('')

    try {
      if (!uploadForm.student_id || !uploadForm.file) {
        setMessage('Pilih mahasiswa dan file yang akan diupload')
        return
      }

      // Validate file
      if (uploadForm.file.type !== 'application/pdf') {
        setMessage('File harus berformat PDF')
        return
      }

      if (uploadForm.file.size > 10 * 1024 * 1024) { // 10MB
        setMessage('Ukuran file maksimal 10MB')
        return
      }

      // In production, upload to server
      console.log('Uploading file:', uploadForm)
      
      setMessage('File berhasil diupload')
      setUploadForm({
        student_id: '',
        file: null
      })
      
      setTimeout(() => {
        setUploadDialog(false)
        setMessage('')
      }, 2000)
    } catch (error) {
      setMessage('Terjadi kesalahan sistem')
    } finally {
      setSubmitting(false)
    }
  }

  if (loading || !user) {
    return <div className="min-h-screen flex items-center justify-center">Loading...</div>
  }

  const studentsWithData = mockStudents.map(student => {
    const score = mockThesisScores.find(s => s.student_id === student.id)
    const draft = mockThesisDrafts.find(d => d.student_id === student.id)
    return { ...student, score, draft }
  })

  const dosenList = mockUsers.filter((u: User) => u.role === 'dosen_pembimbing' || u.role === 'dosen_penguji')

  return (
    <DashboardLayout user={user}>
      <div className="space-y-6">
        <div>
          <h1 className="text-3xl font-bold text-gray-900">Dashboard Admin Prodi</h1>
          <p className="text-gray-600">Kelola data mahasiswa dan upload draft tesis</p>
        </div>

        {/* Statistics */}
        <div className="grid grid-cols-1 md:grid-cols-4 gap-6">
          <Card>
            <CardHeader className="pb-2">
              <CardTitle className="text-sm font-medium text-gray-600">Total Mahasiswa</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold">{mockStudents.length}</div>
            </CardContent>
          </Card>

          <Card>
            <CardHeader className="pb-2">
              <CardTitle className="text-sm font-medium text-gray-600">Draft Terupload</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold">{mockThesisDrafts.length}</div>
            </CardContent>
          </Card>

          <Card>
            <CardHeader className="pb-2">
              <CardTitle className="text-sm font-medium text-gray-600">Nilai Selesai</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold">
                {mockThesisScores.filter(s => s.final_score).length}
              </div>
            </CardContent>
          </Card>

          <Card>
            <CardHeader className="pb-2">
              <CardTitle className="text-sm font-medium text-gray-600">Progress</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold">
                {mockStudents.length > 0 ? 
                  Math.round((mockThesisScores.filter(s => s.final_score).length / mockStudents.length) * 100) : 0}%
              </div>
            </CardContent>
          </Card>
        </div>

        {/* Action Buttons */}
        <div className="flex space-x-4">
          <Dialog open={addStudentDialog} onOpenChange={setAddStudentDialog}>
            <DialogTrigger asChild>
              <Button className="bg-blue-600 hover:bg-blue-700">
                Tambah Mahasiswa
              </Button>
            </DialogTrigger>
            <DialogContent className="max-w-md">
              <DialogHeader>
                <DialogTitle>Tambah Mahasiswa Baru</DialogTitle>
                <DialogDescription>
                  Masukkan data mahasiswa yang akan mengikuti program tesis
                </DialogDescription>
              </DialogHeader>

              <form onSubmit={handleAddStudent} className="space-y-4">
                {message && (
                  <Alert variant={message.includes('berhasil') ? 'default' : 'destructive'}>
                    <AlertDescription>{message}</AlertDescription>
                  </Alert>
                )}

                <div className="space-y-2">
                  <Label htmlFor="nim">NIM</Label>
                  <Input
                    id="nim"
                    value={studentForm.nim}
                    onChange={(e) => setStudentForm({...studentForm, nim: e.target.value})}
                    placeholder="Contoh: 2021001"
                    required
                  />
                </div>

                <div className="space-y-2">
                  <Label htmlFor="name">Nama Lengkap</Label>
                  <Input
                    id="name"
                    value={studentForm.name}
                    onChange={(e) => setStudentForm({...studentForm, name: e.target.value})}
                    placeholder="Nama mahasiswa"
                    required
                  />
                </div>

                <div className="space-y-2">
                  <Label htmlFor="thesis_title">Judul Tesis</Label>
                  <Textarea
                    id="thesis_title"
                    value={studentForm.thesis_title}
                    onChange={(e) => setStudentForm({...studentForm, thesis_title: e.target.value})}
                    placeholder="Judul tesis mahasiswa"
                    required
                  />
                </div>

                <div className="space-y-2">
                  <Label>Dosen Pembimbing</Label>
                  <Select
                    value={studentForm.dosen_pembimbing_id}
                    onValueChange={(value) => setStudentForm({...studentForm, dosen_pembimbing_id: value})}
                  >
                    <SelectTrigger>
                      <SelectValue placeholder="Pilih dosen pembimbing" />
                    </SelectTrigger>
                    <SelectContent>
                      {dosenList.filter((d: User) => d.role === 'dosen_pembimbing').map((dosen: User) => (
                        <SelectItem key={dosen.id} value={dosen.id.toString()}>
                          {dosen.full_name}
                        </SelectItem>
                      ))}
                    </SelectContent>
                  </Select>
                </div>

                <div className="space-y-2">
                  <Label>Dosen Penguji</Label>
                  <Select
                    value={studentForm.dosen_penguji_id}
                    onValueChange={(value) => setStudentForm({...studentForm, dosen_penguji_id: value})}
                  >
                    <SelectTrigger>
                      <SelectValue placeholder="Pilih dosen penguji" />
                    </SelectTrigger>
                    <SelectContent>
                      {dosenList.filter((d: User) => d.role === 'dosen_penguji').map((dosen: User) => (
                        <SelectItem key={dosen.id} value={dosen.id.toString()}>
                          {dosen.full_name}
                        </SelectItem>
                      ))}
                    </SelectContent>
                  </Select>
                </div>

                <div className="flex justify-end space-x-2">
                  <Button
                    type="button"
                    variant="outline"
                    onClick={() => setAddStudentDialog(false)}
                  >
                    Batal
                  </Button>
                  <Button type="submit" disabled={submitting}>
                    {submitting ? 'Menyimpan...' : 'Simpan'}
                  </Button>
                </div>
              </form>
            </DialogContent>
          </Dialog>

          <Dialog open={uploadDialog} onOpenChange={setUploadDialog}>
            <DialogTrigger asChild>
              <Button variant="outline">
                Upload Draft Tesis
              </Button>
            </DialogTrigger>
            <DialogContent>
              <DialogHeader>
                <DialogTitle>Upload Draft Tesis</DialogTitle>
                <DialogDescription>
                  Upload file draft tesis mahasiswa (PDF, maksimal 10MB)
                </DialogDescription>
              </DialogHeader>

              <form onSubmit={handleFileUpload} className="space-y-4">
                {message && (
                  <Alert variant={message.includes('berhasil') ? 'default' : 'destructive'}>
                    <AlertDescription>{message}</AlertDescription>
                  </Alert>
                )}

                <div className="space-y-2">
                  <Label>Pilih Mahasiswa</Label>
                  <Select
                    value={uploadForm.student_id}
                    onValueChange={(value) => setUploadForm({...uploadForm, student_id: value})}
                  >
                    <SelectTrigger>
                      <SelectValue placeholder="Pilih mahasiswa" />
                    </SelectTrigger>
                    <SelectContent>
                      {mockStudents.map((student) => (
                        <SelectItem key={student.id} value={student.id.toString()}>
                          {student.nim} - {student.name}
                        </SelectItem>
                      ))}
                    </SelectContent>
                  </Select>
                </div>

                <div className="space-y-2">
                  <Label htmlFor="file">File Draft Tesis (PDF)</Label>
                  <Input
                    id="file"
                    type="file"
                    accept=".pdf"
                    onChange={(e) => setUploadForm({...uploadForm, file: e.target.files?.[0] || null})}
                    required
                  />
                  <p className="text-xs text-gray-500">Maksimal 10MB, format PDF</p>
                </div>

                <div className="flex justify-end space-x-2">
                  <Button
                    type="button"
                    variant="outline"
                    onClick={() => setUploadDialog(false)}
                  >
                    Batal
                  </Button>
                  <Button type="submit" disabled={submitting}>
                    {submitting ? 'Mengupload...' : 'Upload'}
                  </Button>
                </div>
              </form>
            </DialogContent>
          </Dialog>
        </div>

        {/* Students Table */}
        <Card>
          <CardHeader>
            <CardTitle>Data Mahasiswa</CardTitle>
            <CardDescription>
              Daftar mahasiswa dan status tesis
            </CardDescription>
          </CardHeader>
          <CardContent>
            <Table>
              <TableHeader>
                <TableRow>
                  <TableHead>NIM</TableHead>
                  <TableHead>Nama</TableHead>
                  <TableHead>Judul Tesis</TableHead>
                  <TableHead>Draft</TableHead>
                  <TableHead>Nilai Akhir</TableHead>
                  <TableHead>Status</TableHead>
                </TableRow>
              </TableHeader>
              <TableBody>
                {studentsWithData.map((student) => (
                  <TableRow key={student.id}>
                    <TableCell className="font-medium">{student.nim}</TableCell>
                    <TableCell>{student.name}</TableCell>
                    <TableCell className="max-w-xs truncate">{student.thesis_title}</TableCell>
                    <TableCell>
                      {student.draft ? (
                        <Badge variant="secondary">Terupload</Badge>
                      ) : (
                        <Badge variant="outline">Belum Upload</Badge>
                      )}
                    </TableCell>
                    <TableCell>
                      {student.score?.final_score ? (
                        <span className="font-bold text-green-600">{student.score.final_score}</span>
                      ) : (
                        <span className="text-gray-400">-</span>
                      )}
                    </TableCell>
                    <TableCell>
                      {student.score?.final_score ? (
                        <Badge className="bg-green-100 text-green-800">Selesai</Badge>
                      ) : student.score?.dosbim_score || student.score?.dospenguji_score ? (
                        <Badge className="bg-yellow-100 text-yellow-800">Proses</Badge>
                      ) : (
                        <Badge variant="outline">Belum Mulai</Badge>
                      )}
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </CardContent>
        </Card>
      </div>
    </DashboardLayout>
  )
}
