'use client'

import { useState, useEffect } from 'react'
import { Button } from '@/components/ui/button'
import { Card, CardContent } from '@/components/ui/card'
import { Separator } from '@/components/ui/separator'
import { User } from '@/lib/auth'

interface DashboardLayoutProps {
  children: React.ReactNode
  user: User
}

export default function DashboardLayout({ children, user }: DashboardLayoutProps) {
  const [sidebarOpen, setSidebarOpen] = useState(false)

  const handleLogout = async () => {
    try {
      await fetch('/api/auth/logout', { method: 'POST' })
      window.location.href = '/'
    } catch (error) {
      console.error('Logout error:', error)
    }
  }

  const getNavigationItems = () => {
    const baseItems = [
      { name: 'Dashboard', href: `/dashboard/${user.role.replace('_', '-')}` }
    ]

    switch (user.role) {
      case 'superadmin':
        return [
          ...baseItems,
          { name: 'Kelola Pengguna', href: `/dashboard/superadmin/manage-users` },
          { name: 'Laporan PDF', href: `/dashboard/superadmin/reports` }
        ]
      case 'dosen_pembimbing':
        return [
          ...baseItems,
          { name: 'Mahasiswa Bimbingan', href: `/dashboard/dosen-pembimbing/students` },
          { name: 'Input Nilai', href: `/dashboard/dosen-pembimbing/scores` }
        ]
      case 'dosen_penguji':
        return [
          ...baseItems,
          { name: 'Mahasiswa Ujian', href: `/dashboard/dosen-penguji/students` },
          { name: 'Input Nilai', href: `/dashboard/dosen-penguji/scores` }
        ]
      case 'admin_prodi':
        return [
          ...baseItems,
          { name: 'Tambah Mahasiswa', href: `/dashboard/admin-prodi/add-student` },
          { name: 'Upload Tesis', href: `/dashboard/admin-prodi/upload-thesis` },
          { name: 'Hasil Akhir', href: `/dashboard/admin-prodi/results` }
        ]
      default:
        return baseItems
    }
  }

  const navigationItems = getNavigationItems()

  return (
    <div className="min-h-screen bg-gray-50">
      {/* Mobile menu button */}
      <div className="lg:hidden bg-white shadow-sm border-b p-4">
        <Button
          variant="outline"
          onClick={() => setSidebarOpen(!sidebarOpen)}
          className="w-full justify-start"
        >
          Menu
        </Button>
      </div>

      <div className="flex">
        {/* Sidebar */}
        <div className={`${sidebarOpen ? 'block' : 'hidden'} lg:block w-64 bg-white shadow-lg min-h-screen`}>
          <div className="p-6">
            <h1 className="text-xl font-bold text-gray-900 mb-2">
              Sistem Manajemen Tesis
            </h1>
            <p className="text-sm text-gray-600">
              Program Pascasarjana
            </p>
          </div>

          <Separator />

          <div className="p-4">
            <div className="mb-6">
              <p className="text-sm font-medium text-gray-900">{user.full_name}</p>
              <p className="text-xs text-gray-600 capitalize">
                {user.role.replace('_', ' ')}
              </p>
            </div>

            <nav className="space-y-2">
              {navigationItems.map((item) => (
                <a
                  key={item.name}
                  href={item.href}
                  className="block px-3 py-2 text-sm font-medium text-gray-700 rounded-md hover:bg-gray-100 hover:text-gray-900 transition-colors"
                >
                  {item.name}
                </a>
              ))}
            </nav>

            <Separator className="my-6" />

            <Button
              variant="outline"
              onClick={handleLogout}
              className="w-full"
            >
              Keluar
            </Button>
          </div>
        </div>

        {/* Main content */}
        <div className="flex-1 lg:ml-0">
          <main className="p-6">
            {children}
          </main>
        </div>
      </div>
    </div>
  )
}
