export interface User {
  id: number
  username: string
  email: string
  role: 'superadmin' | 'dosen_pembimbing' | 'dosen_penguji' | 'admin_prodi'
  full_name: string
}

export interface Student {
  id: number
  nim: string
  name: string
  thesis_title: string
  dosen_pembimbing_id: number
  dosen_penguji_id: number
  created_at: string
}

export interface ThesisDraft {
  id: number
  student_id: number
  file_name: string
  file_path: string
  file_size: number
  uploaded_by: number
  uploaded_at: string
}

export interface ThesisScore {
  id: number
  student_id: number
  dosbim_score?: number
  dospenguji_score?: number
  final_score?: number
  dosbim_updated_at?: string
  dospenguji_updated_at?: string
  updated_at: string
}

