// Helper functions
export function getStudentsByDosenPembimbing(dosenId: number): Student[] {
  return mockStudents.filter(student => student.dosen_pembimbing_id === dosenId)
}

export function getStudentsByDosenPenguji(dosenId: number): Student[] {
  return mockStudents.filter(student => student.dosen_penguji_id === dosenId)
}

export function getStudentById(id: number): Student | undefined {
  return mockStudents.find(student => student.id === id)
}

export function getScoreByStudentId(studentId: number): ThesisScore | undefined {
  return mockThesisScores.find(score => score.student_id === studentId)
}

export function getDraftByStudentId(studentId: number): ThesisDraft | undefined {
  return mockThesisDrafts.find(draft => draft.student_id === studentId)
}

export function calculateFinalScore(dosbimScore?: number, dospengujiScore?: number): number | undefined {
  if (dosbimScore !== undefined && dospengujiScore !== undefined) {
    return (dosbimScore + dospengujiScore) / 2
  }
  return undefined
}

export function updateScore(studentId: number, scoreType: 'dosbim' | 'dospenguji', score: number): boolean {
  const scoreIndex = mockThesisScores.findIndex(s => s.student_id === studentId)
  
  if (scoreIndex === -1) {
    // Create new score record
    const newScore: ThesisScore = {
      id: mockThesisScores.length + 1,
      student_id: studentId,
      updated_at: new Date().toISOString()
    }
    
    if (scoreType === 'dosbim') {
      newScore.dosbim_score = score
      newScore.dosbim_updated_at = new Date().toISOString()
    } else {
      newScore.dospenguji_score = score
      newScore.dospenguji_updated_at = new Date().toISOString()
    }
    
    mockThesisScores.push(newScore)
  } else {
    // Update existing score
    const existingScore = mockThesisScores[scoreIndex]
    
    if (scoreType === 'dosbim') {
      existingScore.dosbim_score = score
      existingScore.dosbim_updated_at = new Date().toISOString()
    } else {
      existingScore.dospenguji_score = score
      existingScore.dospenguji_updated_at = new Date().toISOString()
    }
    
    existingScore.updated_at = new Date().toISOString()
    
    // Recalculate final score if both scores are available
    existingScore.final_score = calculateFinalScore(
      existingScore.dosbim_score,
      existingScore.dospenguji_score
    )
  }
  
  return true
}
